import React,{useContext, useEffect} from 'react';

import ComidaContext from '../../Context/comida/DietaContext';

const InformacionComida = () => {

    const comidaContext = useContext(ComidaContext);
    const {comidaInfo, total,consumidas} = comidaContext;

    useEffect(()=>{

      

    },[consumidas,comidaInfo])

    return ( 

        <div className="container">
        <table className="">
        <thead>
          <tr>
              <th>Prote</th>
              <th>Carbohidratos</th>
              <th>Grasas</th>
              <th>Kcal</th>
              <th>Kcal Restantes</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>{(comidaInfo.prote).toFixed(2) || 0}</td>
            <td>{(comidaInfo.carbos).toFixed(2) || 0}</td>
            <td>{(comidaInfo.grasa).toFixed(2) || 0}</td>
            <td>{ (((comidaInfo.prote + comidaInfo.carbos) * 4) + (comidaInfo.grasa * 9)).toFixed(2)  || 0}</td>
            <td>{(total-consumidas).toFixed(2)}</td>
          </tr>
        </tbody>
      </table>
      </div>

     );
}
 
export default InformacionComida;